.product-list {
  .container{
    padding-left:10px;
    padding-right:10px;
    .row{
      display:flex;
      flex-wrap: wrap;
      .product-card {
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
        transition: 0.3s;
        width: calc(25% - 20px);
        margin-left:10px;
        margin-right:10px;
        margin-top:20px;
        margin-bottom:20px;
        &:hover {
          box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
        }
        .product-desc{
          padding: 2px 16px;
          .product-price{
            margin-bottom:20px;
            font-family: 'Courier New', Courier, monospace;
            font-size:x-large;
            display:flex;
            justify-content: space-between;
            .last-price{
              color: gray;
              text-decoration-line: line-through;
            }
            .last-price{
              color:black;
            }
            .new-price{
              color: red;
            }
            .sales-rate{
              background-color:red;
              color:white;
              font-weight:bold;
              border-radius: 20px;
              padding: 2px 2px;
              width:100px;
              text-align:center;
            }
          }
        }
      }
  }}
}
